  -webkit-transform: translate(0px, -300px) rotate(50deg);
          transform: translate(0px, -300px) rotate(50deg);
  -webkit-transition-duration: 5s;
          transition-duration: 5s;
  background-color: rgba(123,121,12,0);
  overflow: hidden;
}
.behind{
  height: 300px;
  width: 400px;
  background-color: blue;
  position: absolute;
/*  left: 405px;
  top: -270px;*/
  -webkit-transform-origin:left bottom;
          transform-origin:left bottom;
  -webkit-transform: translate(405px, -270px) rotate(-25deg);
          transform: translate(405px, -270px) rotate(-25deg);
  -webkit-transition-duration:6s;
          transition-duration:6s;
}
